<template>
    <v-container fluid fill-height style="padding:0;" id="circlepoint" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Explain">解释</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <p class=".body-1">画图的时候注意小点绕圆心轨迹运动，必须把小点的位置和移动后的坐标轴对应起来，具体操作见代码</p>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <canvas id="canvas" width="400px" height="400px"></canvas>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Code1">代码</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                    <v-tab key="html" ripple>HTML</v-tab>
                                    <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                    <v-tab-item key="html">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">
                                                    {{html}}
                                                </pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                    <v-tab-item key="css">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">
                                                    {{js}}
                                                </pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                </v-tabs>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import { setInterval } from 'timers';
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-10-7",
            title: "绕中心点旋转带尾影的点",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            <canvas 
                id="canvas" 
                width="400px" 
                height="400px"
            >
            </canvas>
            `,
            js: `
            const canvas = document.getElementById('canvas');
            const ctx = canvas.getContext('2d');
            
            let ang = 0;
            window.requestAnimationFrame(draw);
            draw();
            function draw() {
                ctx.save();
                ctx.fillStyle = 'rgba(222, 234, 222, 0.1)';
                ctx.fillRect(0, 0, canvas.width, canvas.height);

                // 移动坐标轴原点到中心点
                ctx.translate(200, 200);
                ang+=2;
                ctx.rotate((Math.PI / 180) * ang);
                ctx.beginPath();

                drawRect(-100, 0);
                drawRect(-130, 30);
                drawRect(100, 10);
                drawRect(130, 30);

                ctx.restore();
                window.requestAnimationFrame(draw);
            }

            // 画一个带渐变色的矩形
            function drawRect(x, y) {
                const cg = ctx.createLinearGradient(x, y, x, -10);
                cg.addColorStop(0, 'rgba(0, 128, 0, 1)');
                cg.addColorStop(0.2, 'rgba(0, 128, 0, 0.3)');
                cg.addColorStop(1, 'rgba(0, 128, 0, 0.0)');
                ctx.rect(x, y, 3, 10);
                ctx.fillStyle = cg;
                ctx.fill();
            }
            `
        };
    },
    mounted() {
        const canvas = document.getElementById('canvas');
        const ctx = canvas.getContext('2d');
        
        let ang = 0;
        window.requestAnimationFrame(draw);
        draw();
        function draw() {
            ctx.save();
            ctx.fillStyle = 'rgba(222, 234, 222, 0.1)';
            ctx.fillRect(0, 0, canvas.width, canvas.height);

            // 移动坐标轴原点到中心点
            ctx.translate(200, 200);
            ang+=2;
            ctx.rotate((Math.PI / 180) * ang);
            ctx.beginPath();

            drawRect(-100, 0);
            drawRect(-130, 30);
            drawRect(100, 10);
            drawRect(130, 30);

            ctx.restore();
            window.requestAnimationFrame(draw);
        }

        // 画一个带渐变色的矩形
        function drawRect(x, y) {
            const cg = ctx.createLinearGradient(x, y, x, -10);
            cg.addColorStop(0, 'rgba(0, 128, 0, 1)');
            cg.addColorStop(0.2, 'rgba(0, 128, 0, 0.3)');
            cg.addColorStop(1, 'rgba(0, 128, 0, 0.0)');
            ctx.rect(x, y, 3, 10);
            ctx.fillStyle = cg;
            ctx.fill();
        }
    }
};
</script>

